<template>
    <h3>::: Sign Up :::</h3>
    <v-form>
        <div>
            <v-text-field type="email" v-model="username" prepend-icon="mdi-email"/>
        </div>
        <div>
            <v-text-field type="password" v-model="password" placeholder="PASSWORD"/><br/>
        </div>    
        <div>
            <v-text-field v-model="nickname" placeholder="Nickname"/><br/>
        </div>
        <v-btn @click="submitForm">SignUp</v-btn>
    </v-form>

</template>

<script>
import { registUser } from '@/api/index.js';
import { validEmailAddr} from '@/utils/validation.js';

export default {
    name : 'regist',
    data() {
        return {
            username : '',
            password : '',
            nickname : ''
        }
    },
    methods: {
        submitForm() {
            console.log('username' + username);
            try {
                const userData = {
                    username : this.username,
                    password : this.password,
                    nickname : this.nickname
                }
                const {data} = registUser(userData);
                console.log('response:' + data);
            } catch (error) {
                this.logMessage = error.response.data;        
            } finally {
                this.initForm();
            }
            
        },
        initForm() {
            this.username = '';
            this.password = '';
            this.nickname = '';
        }
    },
    computed: {
        isUsernameValid() {
            return validEmailAddr(this.username);
        }
    }
}
</script>